/* Home Card */
div.home-card-container {
  width: max(90%, 80%);
  margin: 5em auto;
  background-color: var(--light-color);
  box-shadow: 0 0.4em 0.7em var(--dark-rgb);
  border-radius: 0.3em;
}

.home-card-user-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 1em;
  background: linear-gradient(var(--dark-rgb) 40%, var(--light-color) 48%);
}

.home-card-name-container h2 {
  padding: 0;
}

.home-card-img-container img {
  width: clamp(8em, 12em, 13em);
  height: clamp(8em, 12em, 13em);
  transform: translateY(-5em);
  border: 0.1em solid var(--btn-color);
}
/* Create Post */
main {
  position: relative;
}

div.create-post-container {
  display: flex;
  gap: 2em;
  -webkit-gap: 2em;
  align-items: center;
  width: max(90%, 80%);
  margin: 2em auto;
  background-color: var(--light-color);
  box-shadow: 0 0.4em 0.7em var(--dark-rgb);
  padding: 1em;
  border-radius: 0.3em;
}

.create-post-container img {
  width: 3.5em;
  height: 3.5em;
  border-radius: 50%;
}

.create-post-btn {
  text-align: center;
  border: 0.1em solid var(--text-color);
  width: 100%;
  border-radius: 3em;
  padding: 0.5em;
  background-color: transparent;
}

/* Post Modal*/
div.post-modal-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  backdrop-filter: blur(0.2em);
  z-index: 1;
}

div.post-modal-wrapper {
  display: flex;
  flex-direction: column;
  box-shadow: 0 0.4em 0.7em var(--dark-rgb);
  background-color: var(--light-color);
  border-radius: 0.32em;
  gap: 1em;
  -webkit-gap: 1em;
  width: max(80%, 70%);
  margin: auto;
}

.post-modal-wrapper textarea {
  resize: none;
  padding: 1em;
}
.post-modal-container button {
  padding: 0.4em 0.7em;
  margin: 0.7em;
}

.close-modal-btn {
  align-self: flex-start;
}

.post-modal-btn {
  align-self: flex-end;
  background-color: var(--btn-color);
  color: var(--light-color);
}

/* Post Content Section */
.post-section img {
  width: 3em;
  height: 3em;
  border-radius: 50%;
}

div.post-content-container {
  box-shadow: 0 0.4em 0.7em var(--dark-rgb);
  background-color: var(--light-color);
  width: max(80%, 70%);
  margin: 1em auto;
  padding: 1em;
  border-radius: 0.2em;
  position: relative;
}

.post-user-details {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.post-user-details div:first-child {
  display: flex;
  align-items: center;
  gap: 1em;
  -webkit-gap: 1em;
}

.post-user-details div:first-child div:last-child h3 {
  color: var(--btn-color);
  text-decoration: underline;
  cursor: pointer;
}

.post-content {
  padding: 1em;
}

/* Post Interaction */
div.post-interaction {
  display: flex;
  justify-content: space-between;
  padding: 1em;
  align-items: center;
  gap: 1em;
  margin-top: 0.5em;
}

.post-interaction > * {
  cursor: pointer;
}

.user-comment-container {
  display: flex;
  gap: 1em;
  padding: 0.4em;
  margin-top: 1em;
}

.user-comment {
  border: 0.1em solid var(--btn-color);
  border-radius: 0.3em;
  resize: none;
  padding: 0.4em;
  flex: 1;
}

.comment-btn {
  background-color: var(--btn-color);
  border-radius: 0.3em;
  color: var(--light-color);
  align-self: flex-end;
}

div.comment-container {
  margin: auto;
  display: flex;
  flex-direction: column;
  gap: 1em;
  -webkit-gap: 1em;
  padding: 0.5em;
}

.comment-wrapper {
  background-color: var(--dark-rgb);
  padding: 0.6em;
  border-radius: 0.3em;
}

div.edit-delete-container {
  display: flex;
  gap: 1em;
  -webkit-gap: 1em;
  align-items: center;
  padding: 1em;
}

.edit-delete-container > * {
  padding: 0.5em;
  font-size: var(--nav-font);
  cursor: pointer;
}

.edit-delete-container > *:hover {
  background-color: var(--dark-rgb);
  border-radius: 50%;
}
