html {
  font-size: 62.5%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

:root {
  /* font */
  --nav-font: clamp(1rem, 1.7vw, 1.2rem);
  --text-font: clamp(1.2rem, 2vw, 1.6rem);
  --header-font: clamp(2.3rem, 3.5vw, 3.3rem);
  --subhead-font: clamp(1.6rem, 2.5vw, 2.4rem);
  --title-font: clamp(1.3rem, 2.2vw, 2rem);

  --ex-small-font: 0.7rem;
  --small-font: 1rem;
  --medium-font: 1.5rem;
  --large-font: 2rem;
  --ex-large-font: 2.5rem;

  /* color */
  --background-color: #3331;
  --text-color: #111;
  --primary-black: #1113;
  --secondary-black: #1115;
  --header-background: #fff;
  --nav-color: #3457d5;
  --btn-color: var(--nav-color);
  --light-color: var(--header-background);

  /* rgb */
  --dark-rgb: rgb(0, 0, 0, 0.2);
  --light-rgb: rgb(255, 255, 255, 0.2);

  /* transiton */
  --fast-transition: 0.3s;
  --normal-transition: 0.6s;
  --slow-transition: 1s;

  /* box-shadow */

  --box-shadow: box-shadow: 0 0.4em 0.7em var(--dark-rgb);

}

/* section {
  margin-top: 1em;
} */

h1 {
  font-size: var(--header-font);
}

h2 {
  font-size: var(--subhead-font);
  padding: 1em;
}

p {
  font-size: var(--text-font);
}

li {
  list-style: none;
}

input,
textarea {
  outline: none;
  cursor: pointer;
  border: none;
}

a {
  text-decoration: none;
  color: var(--nav-color);
}

a,
button {
  font-size: var(--nav-font);
  cursor: pointer;
}

button {
  border: none;
  border-radius: 0.1em;
  opacity: 0.9;
  font-family: inherit;
  transition: opacity,
    var(--normal-transition) cubic-bezier(0.39, 0.575, 0.565, 1);
  padding: 0.3em 0.5em;
}

button:hover {
  opacity: 1;
}

::placeholder {
  color: var(--dark-rgb);
}

body {
  background: var(--background-color);
  background-repeat: no-repeat;
  color: var(--text-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Header */
header.header-container {
  display: grid;
  grid-template-columns: 1fr 3fr;
  align-items: center;
  background-color: var(--header-background);
  box-shadow: 0 0.3em 0.5em var(--dark-rgb);
  padding: 1em;
}

div.brand-logo-container{
  display: flex;
  align-items: center;
}

div.logo-container{
  display: flex;
  gap: .1em;
}

.left{
  width: 30px;
  height: 30px;
  background-color: var(--btn-color);
  border-radius: 0 100%;
}
.right{
  width: 30px;
  height: 30px;
  background-color: var(--btn-color);
  border-radius: 100% 0;
}

nav.nav-container {
  padding: 1em;
}

.nav-list {
  display: flex;
  justify-content: space-around;
}

.nav-item a {
  color: var(--text-color);
}

img {
  border-radius: 50%;
}

/* Like Section */
.like {
  color: blue;
  animation: scale 0.3s ease-in-out;
}

@keyframes scale {
  to {
    transform: scale(1.1);
  }
}

@media (min-width: 800px) {
  main{
    width: max(85%);
    margin: auto;
  }
}

@media (min-width: 1000px) {
  main{
    width: max(70%);
    margin: auto;
  }
}
