.form-header {
  text-align: center;
}

form.form-container {
  display: flex;
  flex-direction: column;
  width: max(80%, 70%);
  margin: auto;
  gap: 2em;
  -webkit-gap: 2em;
  padding: 2em;
}

form input.form-input {
  padding: clamp(1.5em, 1.7em, 2em);
  border-radius: 0.3em;
  background-color: var(--header-background);
}

form button.form-btn {
  padding: 1em;
  border-radius: 0.3em;
}

.error-msg {
  text-align: center;
  width: max(80%, 70%);
  margin: auto;
  padding: 1em;
  font-size: var(--nav-font);
  background-color: var(--text-color);
  color: var(--header-background);
  font-size: var(--nav-font);
}
