div.profile-img-container {
  height: 25vh;
  background-color: var(--dark-rgb);
  position: relative;
}

.profile-img-container img {
  width: clamp(7em, 10em, 12em);
  height: clamp(7em, 10em, 12em);
  border: 0.1em solid var(--btn-color);
  position: absolute;
  bottom: -3em;
  left: 1.5em;
}

div.profile-card-container {
  box-shadow: var(--box-shadow);
  padding: 1em;
  background-color: var(--light-color);
  margin: 1em;
}

.user-profile {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
}

.user-details {
  display: flex;
  justify-content: space-between;
  padding: 1em;
}

/* Profile Modal */
div.profile-modal-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  backdrop-filter: blur(0.3em);
  z-index: 1;
}

form.profile-modal-form {
  display: flex;
  flex-direction: column;
  gap: 1em;
  -webkit-gap: 1em;
  padding: 1em;
  width: max(70%, 60%);
  margin: auto;
  background-color: var(--background-color);
  border: 0.1em solid var(--text-color);
  border-radius: 0.3em;
}

.profile-modal-form label {
  display: flex;
  gap: 0.5em;
  -webkit-gap: 0.5em;
  flex-direction: column;
  border: 0.1em solid var(--dark-rgb);
  padding: 1em;
}

.profile-modal-form label input,
.profile-modal-form label textarea {
  padding: 1em;
  resize: none;
  border-radius: 0.2em;
}

.profile-modal-form button {
  padding: 0.5em;
  border-radius: 0.2em;
  background-color: var(--text-color);
  color: var(--light-color);
}

.close-profile-modal {
  width: fit-content;
  align-self: flex-end;
  width: 2.5em;
}
